import { Component, h, Prop, State, Listen, Event, EventEmitter, Method } from '@stencil/core';
import priceFormat from '../../../global/functions/price';

@Component({
  tag: 'ks-cart-product',
  styleUrl: 'cartProduct.css'
})
export class CartProduct {
  @Prop({reflect: true}) productId: string;
  @Prop() name: string;
  @Prop() img: string;
  @Prop() link: string;
  @Prop({reflect: true}) small: boolean;
  @Prop() price: number;
  @Prop() shippingTime: string = "";
  @Prop() countUnit: string = "";

  @Prop({reflect: true}) amount: number = 1;
  @Prop() maxAmount: number;

  @Prop() removable: boolean = false;
  @State() loading: boolean = false;


  @Event() removeProduct: EventEmitter<string>;
  onRemoveHandler() {
    this.loading = true;
    this.removeProduct.emit(this.productId);
  }

  @Event() productCount: EventEmitter<[id: string, count: number, last: number]>;
  onCountHandler(detail: any) {
    this.productCount.emit([this.productId, detail.current, detail.last]);
  }

  @Method() async ResetLoading() {
      this.loading = false;
  }

  @State() mobile: number = 0;

  @Listen('resize', { target: 'window' })
  resizeHandler() {
    this.mobile = 
        window.innerWidth < 959 ? 
            window.innerWidth < 640 ? 
            2 : 1
        : 0;
  }

  componentWillLoad() {
      this.resizeHandler();
  }

  render() {
    const price = priceFormat(this.price);
    const inlineBlockOnMobile = {display: this.removable || this.shippingTime ? "block" : "inline-block"};

    return[
        <div class="ks-text-decorated" uk-grid>
            <a class="product-image" href={this.link}>
                <ks-img2 center src={this.img} width={150} height={150} alt="zdjÄ™cie produktu" />
            </a>

            <div class="description">
                <a href={this.link} innerHTML={this.name}></a>

                {this.mobile >= 1 ?
                    <div class="numbers">
                        <div style={inlineBlockOnMobile}>
                            <span class="price">{price}</span>
                            <span class="shipping">{this.shippingTime}</span>
                        </div>

                        <div style={inlineBlockOnMobile}>
                            {this.removable ?
                                <ks-cart-spinner onChanged={(e)=>this.onCountHandler(e.detail)} initial-value={this.amount} max={this.maxAmount} /> :
                                <div class="amount">{this.amount} {this.countUnit}</div>
                            }
                        </div>
                    </div>
                : this.shippingTime != "" ? 
                    <div class="numbers">{this.shippingTime}</div>
                : null}
            </div>
            

            {this.mobile == 0 ? [
                <div class="price">{price}</div>,
                <div class="amount">
                    {this.removable ?
                        <ks-cart-spinner onChanged={(e)=>this.onCountHandler(e.detail)} initial-value={this.amount} max={this.maxAmount} /> :
                        <span>{this.amount} {this.countUnit}</span>
                    }
                </div>
            ] : null}
            

            {this.removable ? 
                this.mobile == 2 ?
                    this.loading ?
                        <div uk-spinner class="uk-position-top-right uk-position-small"></div> :
                        <button type="button" uk-close class="uk-close-large uk-position-top-right uk-position-small" onClick={()=>this.onRemoveHandler()}></button>
                :
                    <nav style={{width: "50px"}} class="uk-text-center">
                        {this.loading ?
                            <div uk-spinner></div> :
                            <button type="button" uk-close class="uk-close-large" onClick={()=>this.onRemoveHandler()}></button>
                        }
                    </nav>
            :null}
        </div>,
        <hr/>
    ];
  }
}
