import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-cart-product-heading'
})
export class CartProductHeading {
  @Prop() removable: boolean;
  @Element() root: HTMLElement;
  
  @Prop() productTableNames: string = "";
  @Prop() productTablePrices: string = "";
  @Prop() productTableCount: string = "";
  @Prop() productTableRemove: string = "";


  componentDidLoad() { 
    this.root.style.display = "block";
  }

  render() {
    return[
        <div class="uk-flex uk-flex-middle ks-text-decorated" uk-grid style={{fontSize: "18px"}}>
            <div class="uk-width-expand uk-text-bold" style={{fontSize: "22px"}}>{this.productTableNames}</div>

            <div style={{width: "100px"}} class="uk-text-center uk-visible@m">{this.productTablePrices}</div>
            <div style={{width: "100px"}} class="uk-text-center uk-visible@m">{this.productTableCount}</div>

            {this.removable ? 
                <div style={{width: "50px"}} class="uk-text-center">{this.productTableRemove}</div>
            : null}
        </div>,
        <hr />        
    ];
  }
}
