ks-cart-product {
    display: block;
}

ks-cart-product > div {
    display: flex;
    align-items: center;
    font-size: 18px;
}

ks-cart-product .description {
    flex: 1;
}

ks-cart-product .description a {
    color: inherit !important;
    text-decoration: none !important;
}

ks-cart-product .numbers {
    font-weight: 700;
}

ks-cart-product .price {
    text-align: center;
    width: 100px;
}

ks-cart-product .amount {
    text-align: center;
    width: 120px;
    margin-left: 15px;
}

ks-cart-product .product-image {
    width: 150px;
    height: 150px;
    max-width: 150px;
    max-height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}

ks-cart-product[small] .product-image {
    width: 90px;
    height: 90px;
    max-width: 90px;
    max-height: 90px;
}


@media only screen and (max-width: 959px) {
    ks-cart-product .price {
        text-align: initial;
        width: initial;

        color: #e21334;
    }
    
    ks-cart-product .shipping {
        margin-left: 10px;
    }

    ks-cart-product .amount {
        text-align: initial;
        width: initial;
        margin-left: 0px;
    }

    ks-cart-product ks-cart-spinner {
        margin-top: 10px;
    }
}

@media only screen and (max-width: 640px) {
    ks-cart-product > div {
        flex-direction: column;
        position: relative;
    }

    ks-cart-product .description {
        text-align: center;
    }

    ks-cart-product .shipping {
        display: block;
        font-size: 16px;
        margin-bottom: 5px;
        margin-left: 0px;
        margin-top: -3px;
    }

    ks-cart-product .numbers {
        margin-top: 7px;
    }

    ks-cart-product .amount {
        text-align: center;
    }

    ks-cart-product[small] .amount {
        font-size: 16px;
    }

    ks-cart-product[small] .shipping {
        margin-bottom: 0px;
    }
}
