import { Component, h, Prop, Event, EventEmitter, Method, State, Element } from '@stencil/core';

@Component({
  tag: 'ks-cart-spinner',
  styleUrl: 'cartSpinner.css'
})
export class CartSpinner {
  @Element() root: HTMLElement;

  @Prop() name: string;
  @Prop({ mutable: true, reflect: true }) initialValue: number = 1;
  @Prop() max: number;

  @State() value: number = 1;
  componentWillLoad() {
    this.value = this.initialValue;
  }

  Increment() {
    const last = this.value;

    if(typeof this.max === "undefined" || this.value < this.max) {
      this.value += 1;

      this.onChangedHandler(this.value, last);
    }
  }

  Decrement() {
    const last = this.value;

    if(this.value > 1) {
      this.value -= 1;

      this.onChangedHandler(this.value, last);
    }
  }

  Change(e: Event) {
    const last = this.value;
    const input = e.target as HTMLInputElement;

    const parsed = parseInt(input.value);

    if(!isNaN(parsed) && parsed > 0 && (typeof this.max === "undefined"  || parsed <= this.max))
      this.value = parsed;

    else if (parsed > this.max ) {
      this.value = this.max;
      input.value = this.max.toString();
    }

    else {
      this.value = 1;
      input.value = "1";
    }

    this.onChangedHandler(this.value, last);
  }

  timeout: number;

  @Event() changed: EventEmitter;
  onChangedHandler(current: number, last: number) {
    this.changed.emit({current, last});
  }

  @Method() async SetAmount(amount: number) {
    this.value = amount;
  }

  render() {
    return(
      this.max == 1 ? 
        <div>1 szt.</div> :
        <div>
            <button class="uk-button uk-button-muted" onClick={()=>this.Decrement()}>-</button>
            <input type="text" maxlength="3" value={this.value} name={this.name}
                   onChange={(e)=>this.Change(e)}
                   class="uk-input"/>
            <button class="uk-button uk-button-muted" onClick={()=>this.Increment()}>+</button>
        </div>
    );
  }
}
